.App {
    text-align: center;
  }
  body{
      display: flex;
      height: 100vh;
      width: 100%;
      justify-content: center;
      align-items: center;
      background-image: linear-gradient(to bottom right, #5cc8ff 0%, #d63c6b 100%);
      background-repeat: no-repeat;
      background-attachment: fixed;
  }
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
    height: 40vmin;
    pointer-events: none;
  }
  
  .App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
  }
  .button{
     margin: 0 5px;
     background-color: #e5657e;
     color: #fff;
     padding: 14px 24px; 
     outline: none;
     border: none;
  }
  .reset{
      position: absolute;
      top: 10px;
      right: 10px;
      background-color: #e5657e;
      color: #fff;
      padding: 7px 14px; 
      outline: none;
      border: none;
  }
  .burge{
      margin: 0 5px;
      color: #fff;
      font-size: 30px;
  }
  .App-link {
    color: #61dafb;
  }
  
  @keyframes App-logo-spin {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }
  